@import "./src/utils/variables.module.scss";

@font-face {
  font-family: portfolio_regular;
  src: url(../public/fonts/SpaceGrotesk-Regular.ttf);
}
@font-face {
  font-family: portfolio_bold;
  src: url(../public/fonts/SpaceGrotesk-Bold.ttf);
}

$primaryColor: #915eff;
$secondaryColor: #58379e;

*{
  font-family: portfolio_regular, portfolio_bold, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

h1,h2,h3,h4,h5,h6{
  font-family: portfolio_bold;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active{
    -webkit-background-clip: text;
    -webkit-text-fill-color: #ffffff;
    transition: background-color 5000s ease-in-out 0s;
    box-shadow: inset 0 0 45px 45px $tertiaryColor;
}

.main_color{
  color: $primaryColor;
}

.MuiTimelineContent-root{
  &:nth-child(2n){
    justify-content: right;
  }
}